import { useState } from "react";
import DashboardSection from '../../Components/DashboardSection';
import BannerSection from '../../Components/BannerSection';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import "./charge.css"
import ACProgress from '../../Components/ACProgress'

const Charge: React.FC = () => {
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();

    const [isCharging, setcharge] = useState(false);
    const [start_date, setstart_date] = useState("-");
    const [duration, setduration] = useState("-");
    const [current_L1, setcurrent_L1] = useState("0");
    const [current_L2, setcurrent_L2] = useState("0");
    const [current_L3, setcurrent_L3] = useState("0");
    const [voltage_L1, setvoltage_L1] = useState("0");
    const [voltage_L2, setvoltage_L2] = useState("0");
    const [voltage_L3, setvoltage_L3] = useState("0");
    const [power, setpower] = useState("0");
    const [energy, setenergy] = useState("0");
    const [card_type, setcard_type] = useState("");



    return (
        <div>
            <div className="container-dash">
                <DashboardSection active="charge" />
                <div className='container-right'>
                    <BannerSection title={t("charge-details")} />
                    <div className='dashboard-container-2'>
                        <div className='charge-container'>
                            <div className="charge-item-1">
                                <div className='start-date-header weight-700'>{t("start-date")}</div>
                                <div className='start-date-header'>{start_date}</div>
                                <div className='text-header weight-700'>{t("current-L1")}</div>
                                <div className='text-header'>{current_L1}<sup>A</sup></div>
                                <div className='text-header weight-700'>{t("current-L2")}</div>
                                <div className='text-header'>{current_L2}<sup>A</sup></div>
                                <div className='text-header weight-700'>{t("current-L3")}</div>
                                <div className='text-header'>{current_L3}<sup>A</sup></div>
                                <div className='text-header weight-700'>{t("energy")}</div>
                                <div className='text-header'>{energy}<sup>kwh</sup></div>
                            </div>
                            <div className="charge-item-2">
                                <ACProgress energy={energy} isCharging={isCharging} />


                            </div>
                            <div className="charge-item-3">
                                <div className='start-date-header weight-700'>{t("time")}</div>
                                <div className='start-date-header'>{duration}</div>
                                <div className='text-header weight-700'>{t("voltage-L1")}</div>
                                <div className='text-header'>{current_L1}<sup>V</sup></div>
                                <div className='text-header weight-700'>{t("voltage-L2")}</div>
                                <div className='text-header'>{current_L2}<sup>V</sup></div>
                                <div className='text-header weight-700'>{t("voltage-L3")}</div>
                                <div className='text-header'>{current_L3}<sup>V</sup></div>
                                <div className='text-header weight-700'>{t("power")}</div>
                                <div className='text-header'>{power}<sup>kw</sup></div>
                            </div>
                        </div>
                        {card_type == "" &&
                            <div className="charge-button" style={{ background: isCharging ? "#CF182C" : "#61AE36"}}>{isCharging ? t("stop") : t("start")}</div>
                        }

                    </div>
                </div>
            </div>
        </div>
    );
};

export default Charge;
