import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const WifiConfiguration: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [wifiEnable, setWifiEnable] = useState<boolean>(false);
    const [mod, setMod] = useState("");
    const [ssid, setSSID] = useState("");
    const [password, setPassword] = useState("");
    const [encryptionType, setEncryptionType] = useState("");
    const [wifidhcpcEnable, setDhcpcEnable] = useState<boolean>(false);
    const [ip, setIp] = useState("");
    const [netmask, setNetmask] = useState("");
    const [gateway, setGateway] = useState("");
    const [ssidError, setSsidError] = useState<string | null>(null);
    const [passwordError, setPasswordError] = useState<string | null>(null);
    const [ipError, setIpError] = useState<string | null>(null);
    const [netmaskError, setNetmaskError] = useState<string | null>(null);
    const [gatewayError, setGatewayError] = useState<string | null>(null);

    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    function handleSsidChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setSSID(value);
        if (isValidSsid(value)) {
            setSsidError(null);
        } else {
            setSsidError(t("SSID length must be between 1 and 32 characters"));
        }
    }

    function handlePasswordChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setPassword(value);
        if (isValidPassword(value)) {
            setPasswordError(null);
        } else {
            setPasswordError(t("Password length must be between 8 and 64 characters"));
        }
    }

    function handleIpChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setIp(value);
        if (isValidIp(value)) {
            setIpError(null);
        } else {
            setIpError(t("Invalid IP"));
        }
    }

    function handleNetmaskChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setNetmask(value);
        if (isValidIp(value)) {
            setNetmaskError(null);
        } else {
            setNetmaskError(t("Invalid subnet mask"));
        }
    }

    function handleGatewayChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setGateway(value);
        if (isValidIp(value)) {
            setGatewayError(null);
        } else {
            setGatewayError(t("Invalid IP"));
        }
    }

    function isValidSsid(value: string): boolean {
        return value.length > 0 && value.length <= 32;
    }

    function isValidPassword(value: string): boolean {
        return value.length >= 8 && value.length <= 64;
    }

    function isValidIp(value: string): boolean {
        const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
        return ipPattern.test(value);
    }

    function handleWifiLabelClick() {
        setWifiEnable(!wifiEnable);
    }

    function handleDhcpLabelClick() {
        setDhcpcEnable(!wifidhcpcEnable);
    }


    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("wifi-configuration")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (

                <div className="priority-section">
                    <div className='selection-input'>
                        <div className='sub-header'>
                            <div>{t("wifi-enable")}</div>
                            <input className={`checkbox ${wifiEnable ? 'checked' : ''}`} type="checkbox" checked={wifiEnable} onChange={handleWifiLabelClick} />
                            <img className='tik' src="/assets/img/tik.png" alt="" />
                        </div>
                    </div>

                    <div className='sub-header'>{t("mode-selection")}</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select' disabled={!wifiEnable} value={mod} onChange={(e) => setMod(e.target.value)}>
                            <option value="STA">STA</option>
                            <option value="AP">AP</option>
                        </select>
                    </div>

                    <div className='sub-header'>{t("ssid")}</div>

                    <input name='ssid' className={`input ${ssidError ? 'is-invalid' : ''}`} type="text" disabled={!wifiEnable} value={ssid} onChange={handleSsidChange} />

                    <div className='sub-header'>{t("password")}</div>

                    <input name='password' className={`input ${passwordError ? 'is-invalid' : ''}`} type="text" disabled={!wifiEnable || encryptionType === "OPEN"} value={password} onChange={handlePasswordChange} />

                    <div className='sub-header'>{t("encryption")}</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select name="encryption" className='custom-select' disabled={!wifiEnable} value={encryptionType} onChange={(e) => setEncryptionType(e.target.value)}>
                            <option value="OPEN">OPEN</option>
                            <option value="WEP">WEP</option>
                            <option value="WPA">WPA</option>
                            <option value="WPA2">WPA2</option>
                        </select>
                    </div>

                    <div className='selection-input'>
                        <div className='sub-header'>
                            <div>{t("wifi-dhcpc-enable")}</div>
                            <input className={`checkbox ${wifidhcpcEnable ? 'checked' : ''}`} type="checkbox" checked={wifidhcpcEnable} onChange={(e) => setDhcpcEnable(e.target.checked)} />
                            <img className='tik' src="/assets/img/tik.png" alt="" />
                        </div>
                    </div>

                    <div className='sub-header'>{t("ip")}</div>

                    <input name='ip' className={`input ${ipError ? 'is-invalid' : ''}`} type="text" disabled={!(wifiEnable && !wifidhcpcEnable)} value={ip} onChange={handleIpChange} />

                    <div className='sub-header'>{t("netmask")}</div>

                    <input name='netmask' className={`input ${netmaskError ? 'is-invalid' : ''}`} type="text" disabled={!(wifiEnable && !wifidhcpcEnable)} value={netmask} onChange={handleNetmaskChange} />

                    <div className='sub-header'>{t("gateway")}</div>

                    <input name='gateway' className={`input ${gatewayError ? 'is-invalid' : ''}`} type="text" disabled={!(wifiEnable && !wifidhcpcEnable)} value={gateway} onChange={handleGatewayChange} />

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default WifiConfiguration;


