import React from 'react';
import "./dashboard.css"
import { useTranslation } from "react-i18next";
import DashboardSection from '../../Components/DashboardSection';
import BannerSection from '../../Components/BannerSection';
import { useNavigate } from 'react-router-dom';

const Dashboard = () => {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();

  function openCharge() {
    navigate("/charge")
  }

  function openHardware() {
    navigate("/hardware")
  }

  function openSoftware() {
    navigate("/software")
  }

  function openDevice() {
    navigate("/device")
  }

  function openProfile() {
    navigate("/profile")
  }

  return (
    <div className="container-dash">
      <DashboardSection active="dashboard" />
      <div className='container-right'>
        <BannerSection title={t("dashboard")} />
        <div className='dashboard-container-2'>
          <div className="dash-section-2" onClick={openCharge}>
            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
            <img className="dash-icon-2" src="/assets/img/charge_active.png" alt="" />
            <div className="dash-text-2">{t("charge-details")}</div>
            <img className="ok" src="/assets/img/ok-right.png" alt="" />
          </div>

          <div className="dash-section-2" onClick={openHardware}>
            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
            <img className="dash-icon-2" src="/assets/img/hardware_active.png" alt="" />
            <div className="dash-text-2">{t("hardware-settings")}</div>
            <img className="ok" src="/assets/img/ok-right.png" alt="" />
          </div>

          <div className="dash-section-2" onClick={openSoftware}>
            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
            <img className="dash-icon-2" src="/assets/img/software_active.png" alt="" />
            <div className="dash-text-2">{t("software-settings")}</div>
            <img className="ok" src="/assets/img/ok-right.png" alt="" />
          </div>

          <div className="dash-section-2" onClick={openDevice}>
            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
            <img className="dash-icon-2" src="/assets/img/device_active.png" alt="" />
            <div className="dash-text-2">{t("device-status")}</div>
            <img className="ok" src="/assets/img/ok-right.png" alt="" />
          </div>

          <div className="dash-section-2" onClick={openProfile}>
            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
            <img className="dash-icon-2" src="/assets/img/profile_active.png" alt="" />
            <div className="dash-text-2">{t("profile")}</div>
            <img className="ok" src="/assets/img/ok-right.png" alt="" />
          </div>

        </div>
      </div>


      {/* 

        

        

        

        */}
      {/* </div> */}
    </div>
  );
};

export default Dashboard;