import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';
import { toast } from "react-toastify";

const GetFirmwareVersion: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [version, setversion] = useState("");


    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("get-version")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='sub-header'>{t("version")}</div>

                    <input disabled name='version' className="input" type="text" onChange={(e) => setversion(e.target.value)} value={version} />

                </div>
            )}
        </div>
    );
};

export default GetFirmwareVersion;


