import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const NetworkPrioritySelection: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);

    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("network-priority-selection")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='sub-header'>{t("priority-no")} 1</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select'>
                            <option value="ETH">ETH</option>
                            <option value="WLAN">WLAN</option>
                            <option value="4G">4G</option>
                        </select>
                    </div>


                    <div className='sub-header'>{t("priority-no")} 2</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select'>
                            <option value="ETH">ETH</option>
                            <option value="WLAN">WLAN</option>
                            <option value="4G">4G</option>
                        </select>
                    </div>

                    <div className='sub-header'>{t("priority-no")} 3</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select'>
                            <option value="ETH">ETH</option>
                            <option value="WLAN">WLAN</option>
                            <option value="4G">4G</option>
                        </select>
                    </div>

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default NetworkPrioritySelection;


