import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';
import { toast } from "react-toastify";

const OperationMode: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [card_type, setcard_type] = useState("");
    const [whether_to_open_the_qr_code_process, setwhether_to_open_the_qr_code_process] = useState("");
    const [local_startup_whether_to_go_ocpp_background, setlocal_startup_whether_to_go_ocpp_background] = useState("");
    const [whether_to_transfer_private_data, setwhether_to_transfer_private_data] = useState("");


    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("operation-mode")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select' onChange={(e) => setcard_type(e.target.value)} value={card_type}>
                            <option value="StartStopCard">{t("start-stop-card")}</option>
                            <option value="LocalPnC">{t("local-pnc")}</option>
                            <option value="BillingCard">{t("billing-card")}</option>
                        </select>
                    </div>

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default OperationMode;


