import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const BluetoothSettings: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [bluetooth_enable, setbluetooth_enable] = useState("");
    const [pin, setpin] = useState("");
    const [bluetooth_name, setbluetooth_name] = useState("");



    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };



    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("bluetooth")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='sub-header'>{t("bluetooth-enable")}</div>

                    <div className='selection-input'>
                        <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
                        <select className='custom-select' onChange={(e) => setbluetooth_enable(e.target.value)} value={bluetooth_enable}>
                            <option value="Disable">{t("disable")}</option>
                            <option value="Enable">{t("enable")}</option>
                        </select>
                    </div>

                    <div className='sub-header'>{t("bluetooth-name")}</div>

                    <input name='bluetooth' className="input" type="text" onChange={(e) => setbluetooth_name(e.target.value)} value={bluetooth_name}/>

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default BluetoothSettings;


