import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import "./login.css";
import { toast } from "react-toastify";

const Login = () => {
    const [UserName, setUserName] = useState("");
    const [Password, setPassword] = useState("");
    const [isPasswordVisible, setPasswordVisibility] = useState(false);
    const [type_input, settype] = useState("password");
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();

    useEffect(() => {
        if (localStorage.getItem('isLoggedIn') === 'true') {
            // navigate('/dashboard');
        }
    }, [navigate]);

    useEffect(() => {
        if (isPasswordVisible) {
            settype('text');
        } else {
            settype('password');
        }
    }, [isPasswordVisible]);

    function ChangeLanguage() {
        var lang = ""
        console.log(localStorage.getItem('lang'))
        if (localStorage.getItem('lang') === 'en') {
            lang = "tr"
        }
        else if (localStorage.getItem('lang') === 'tr') {
            lang = "en"
        }
        i18n.changeLanguage(lang);
        localStorage.setItem("lang", lang);
    }

    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault();

        // TEST AMAÇLI
        console.log("username", UserName)
        console.log("password", Password)
        // if (username === 'admin' && password === 'password') {
        //     localStorage.setItem('isLoggedIn', 'true');
        //     navigate('/dashboard');
        // } else {
        //     alert('Yanlış kullanıcı adı veya şifre');
        // }

        // --------------   yazılacak  gerçek yer
        try {
   
            const response = await fetch('/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ UserName, Password }),
            });
            const data = await response.json();
            if (response.ok) {
                localStorage.setItem('token', data.token); // Tokenı localStorage'a kaydet
                console.log('Login successful');
                navigate('/dashboard');
            } else {
                console.log(data.message || t("email-or-password-wrong"))
                toast.error(data.message || t("email-or-password-wrong"));
            }
        } catch (error) {
            console.log('Login error:', error);
            toast.error(t("email-or-password-wrong"));
        }


    };

    return (
        <div className='fullscreen-div'>
            <div className="div-40">
                <div className="col-20">
                    <img className="logo" src="/assets/img/logo.png" alt="Logo" />
                </div>
                <div className="col-70">
                    <div className='login-container'>
                        <div className="sign-in-text">{t("sign-in")}</div>
                        <div className="enter-email">{t("enter-email")}</div>
                        <div className='input-container'>
                            <div className="username-text">{t("username")}</div>
                            <input type="email"
                                className="username-input"
                                placeholder={t("username-input")}
                                aria-label={t("username-input")}
                                onChange={(e) => setUserName(e.target.value)}
                            />
                        </div>
                        <div className='input-container'>
                            <div className="username-text">{t("password")}</div>
                            <input type={type_input}
                                className="username-input"
                                placeholder={t("password-input")}
                                aria-label={t("password-input")}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                            <img
                                src="/assets/img/pass_eye.png"
                                alt="Show password"
                                className="password-eye"
                                onMouseDown={() => setPasswordVisibility(true)}
                                onMouseUp={() => setPasswordVisibility(false)}
                                onMouseLeave={() => setPasswordVisibility(false)}
                            />
                        </div>
                        <div className='login-button' onClick={handleLogin}>{t("sign-in")}</div>
                    </div>
                </div>
                <div className="col-10">
                    <div className="lang-section" onClick={ChangeLanguage}>{t("language")} <span>&gt;</span></div>
                </div>
            </div>
            <div className="div-60">
                <img className="responsive-image" src="/assets/img/car.png" alt="Car" />
            </div>

        </div>
    );
};

export default Login;