import { useState, useEffect } from "react";
import DashboardSection from '../../Components/DashboardSection';
import BannerSection from '../../Components/BannerSection';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import "./hardware.css"

const Hardware: React.FC = () => {
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();
    const [maxcurrent, setMaxCurrent] = useState("");
    const [mid_id, setMidId] = useState("");
    const [externalMid, setExternalMid] = useState<boolean>(false);

    const [isOn, setIsOn] = useState(false);
    const toggleSwitch = () => setIsOn(!isOn);

    function handleInputChangeMaxCurrent(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        if (value === "" || /^\d+$/.test(value)) {
            setMaxCurrent(value);
        }

    }

    function handleInputChangeMid(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        if (value === "" || /^\d+$/.test(value)) {
            setMidId(value);
        }

    }


    return (
        <div>
            <div className="container-dash">
                <DashboardSection active="hardware" />
                <div className='container-right'>
                    <BannerSection title={t("hardware-settings")} />
                    <div className='dashboard-container-2'>
                        <div className="hardware">
                            <div className='header-hardware'>{t("ac-current")}</div>
                            <input className='input' type="text" value={maxcurrent} onChange={handleInputChangeMaxCurrent} placeholder={t("Please enter a number between 6 and 32")} />
                            <div className='header-hardware'>{t("ext-mid-meter")}</div>
                            <label className="switch">
                                <input type="checkbox" checked={isOn} onChange={toggleSwitch} />
                                <span className="slider round"></span>
                            </label>
                            <input className='input'  type="text" value={mid_id} onChange={handleInputChangeMid} placeholder={t("Please enter a number between 2 and 247")} disabled={!isOn}/>
                            <div className="save-button">{t("save")}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Hardware;
