import React, { useState, useEffect } from 'react';
import DashboardSection from '../../Components/DashboardSection';
import BannerSection from '../../Components/BannerSection';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import "./device.css"


const Device: React.FC = () => {
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();

    const [linkStatus, setlinkStatus] = useState("");
  const [strenghtOf4G, setstrenghtOf4G] = useState("");
  const [networkCard, setnetworkCard] = useState("");
  const [stateOfOcpp, setstateOfOcpp] = useState("");

    return (
        <div>
            <div className="container-dash">
                <DashboardSection active="device" />
                <div className='container-right'>
                    <BannerSection title={t("device-status")} />
                    <div className='dashboard-container-2'>
                        <div className='device-header'>{t("network-state")}</div>

                        <div className="dash-section-3" >
                            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
                            <img className="dash-icon-3" src="/assets/img/link.png" alt="" />
                            <div className="dash-text-2">{t("link-status")}</div>
                            <div className='dash-text-3' style={{color: `${linkStatus === "Online" ? "green" : "red"}`,}}>{t(linkStatus)}</div>
                        </div>

                        <div className="dash-section-3" >
                            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
                            <img className="dash-icon-3"  src="/assets/img/4g.png" alt="" />
                            <div className="dash-text-2">{t("strength-of-4g")}</div>
                            <div className='dash-text-3' >{t(strenghtOf4G)}</div>
                        </div>


                        <div className="dash-section-3" >
                            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
                            <img className="dash-icon-3" src="/assets/img/network.png" alt="" />
                            <div className="dash-text-2">{t("network-card")}</div>
                            <div className='dash-text-3' >{t(networkCard)}</div>
                        </div>

                        <div className='device-header'>{t("state-of-ocpp")}</div>

                        <div className="dash-section-3" >
                            <img className="dash-container-img" src="/assets/img/dashboard_container.png" alt="" />
                            <img className="dash-icon-3" src="/assets/img/ocpp.png" alt="" />
                            <div className="dash-text-2">{t("background-connection")}</div>
                            <div className='dash-text-3' style={{color: `${stateOfOcpp === "Online" ? "green" : "red"}`,}}>{t(stateOfOcpp)}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Device;
