import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const FourGConfiguration: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [apn, setApn] = useState("");
    const [user, setUser] = useState("");
    const [password, setPassword] = useState("");
    const [pin, setPin] = useState("");
    const [pinError, setPinError] = useState<string | null>(null);
    const [activate, setActivate] = useState<boolean>(false);


    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    function handlePinChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        if (/^\d{0,6}$/.test(value)) {
            setPin(value);
            setPinError(null);
        } else {
            setPinError(t("Pin must be between 4 and 6 digits and numeric"));
        }
    }

    function handleLabelClick() {
        setActivate(!activate);
    }



    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("4g-configuration")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='selection-input'>
                        <div className='sub-header'>
                            <div>{t("enable-modification")}</div>
                            <input className={`checkbox ${activate ? 'checked' : ''}`} type="checkbox" checked={activate} onChange={(e) => setActivate(e.target.checked)} />
                            <img className='tik' src="/assets/img/tik.png" alt="" />
                        </div>
                    </div>

                    <div className='sub-header'>{t("apn")}</div>

                    <input name='apn' className="input" type="text" onChange={(e) => setApn(e.target.value)} value={apn} disabled={!activate}/>

                    <div className='sub-header'>{t("user")}</div>

                    <input name='user' className="input" type="text" onChange={(e) => setUser(e.target.value)} value={user} disabled={!activate}/>

                    <div className='sub-header'>{t("password")}</div>

                    <input name='password' className="input" type="text" onChange={(e) => setPassword(e.target.value)} value={password} disabled={!activate}/>

                    <div className='sub-header'>{t("pin")}</div>

                    <input name='pin' className="input" type="text" onChange={handlePinChange} value={pin} placeholder={t("Pin must be between 4 and 6 digits and numeric")} disabled={!activate}/>

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default FourGConfiguration;


