import React, { useState, useEffect } from 'react';
import DashboardSection from '../../Components/DashboardSection';
import NetworkPrioritySelection from '../../Components/NetworkPrioritySelection';
import BannerSection from '../../Components/BannerSection';
import EthernetConfiguration from '../../Components/EthernetConfiguration';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import "./software.css"
import WifiConfiguration from '../../Components/WifiConfiguration';
import TimeZoneSettings from '../../Components/TimeZoneSettings';
import BluetoothSettings from '../../Components/BluetoothSettings';
import FourGConfiguration from '../../Components/FourGConfiguration';
import DNSConfiguration from '../../Components/DNSConfiguration';
import OcppSettings from '../../Components/OcppSettings';
import OperationMode from '../../Components/OperationMode';
import GetFirmwareVersion from '../../Components/GetFirmwareVersion';

const Software: React.FC = () => {
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();

    return (
        <div>
            <div className="container-dash">
                <DashboardSection active="software" />
                <div className='container-right'>
                    <BannerSection title={t("software-settings")} />
                    <div className='dashboard-container-2'>
                        <div className='soft-container'>
                            <div className='soft-item'>
                                <NetworkPrioritySelection />
                                <EthernetConfiguration />
                                <WifiConfiguration />
                                <TimeZoneSettings />
                                <BluetoothSettings />
                                <div className='gap'></div>
                            </div>
                            <div className='soft-item'>
                                <FourGConfiguration/>
                                <DNSConfiguration/>
                                <OcppSettings />
                                <OperationMode />
                                <GetFirmwareVersion />
                                <div className='gap'></div>
                            </div>
                        </div>
                        
                    </div>
                    
                </div>
                
            </div>
        </div>
    );
};


export default Software;
