import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const EthernetConfiguration: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [dhcpcEnable, setdhcpcEnable] = useState<boolean>(true);
    const ethernetEnable = true;
    const [ip, setip] = useState("");
    const [netmask, setnetmask] = useState("");
    const [gateway, setgateway] = useState("");
    const [ipError, setIpError] = useState<string | null>(null);
    const [netmaskError, setNetmaskError] = useState<string | null>(null);
    const [gatewayError, setGatewayError] = useState<string | null>(null);


    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    const handleCheckboxChange = () => {
        setdhcpcEnable(!dhcpcEnable);
    };

    function isValidIp(value: string): boolean {
        const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
        return ipPattern.test(value);
    }


    function handleIpChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setip(value);
        if (isValidIp(value)) {
            setIpError(null);
        } else {
            setIpError(t("Invalid IP"));
        }
    }

    function handleNetmaskChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setnetmask(value);
        if (isValidIp(value)) {
            setNetmaskError(null);
        } else {
            setNetmaskError(t("Invalid subnet mask"));
        }
    }

    function handleGatewayChange(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setgateway(value);
        if (isValidIp(value)) {
            setGatewayError(null);
        } else {
            setGatewayError(t("Invalid gateway"));
        }
    }

    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("ethernet-configuration")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (

                <div className="priority-section">
                    <div className='selection-input'>
                        <div className='sub-header'>
                            <div>{t("wifi-dhcpc-enable")}</div>
                            <input className={`checkbox ${dhcpcEnable ? 'checked' : ''}`} type="checkbox" checked={dhcpcEnable} onChange={handleCheckboxChange} />
                            <img className='tik' src="/assets/img/tik.png" alt="" />
                        </div>
                    </div>

                    <div className='sub-header'>{t("ip")}</div>

                    <input name='ip' className='input' type="text" disabled={!(ethernetEnable && !dhcpcEnable)} value={ip} onChange={handleIpChange} />

                    <div className='sub-header'>{t("subnet-mask")}</div>

                    <input name='subnet' className='input' type="text" disabled={!(ethernetEnable && !dhcpcEnable)} value={netmask} onChange={handleNetmaskChange} />

                    <div className='sub-header'>{t("gateway")}</div>

                    <input name='gateway' className='input' type="text" disabled={!(ethernetEnable && !dhcpcEnable)} value={gateway} onChange={handleGatewayChange} />

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default EthernetConfiguration;


