import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';


const DNSConfiguration: React.FC = () => {
    const { t, i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(true);
    const [dnsEnable, setdnsEnable] = useState<boolean>(false);
    const [DNS1, setDNS1] = useState("");
    const [DNS2, setDNS2] = useState("");
    const [dns1Error, setDns1Error] = useState<string | null>(null);
    const [dns2Error, setDns2Error] = useState<string | null>(null);

    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    function handleDns1Change(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setDNS1(value);
        if (isValidIp(value)) {
          setDns1Error(null);
        } else {
          setDns1Error(t("Invalid DNS address"));
        }
      }
    
      function handleDns2Change(e: React.ChangeEvent<HTMLInputElement>) {
        const value = e.target.value;
        setDNS2(value);
        if (isValidIp(value)) {
          setDns2Error(null);
        } else {
          setDns2Error(t("Invalid DNS address"));
        }
      }
    
      function isValidIp(value: string): boolean {
        const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
        return ipPattern.test(value);
      }
    
      function handleDnsLabelClick() {
        setdnsEnable(!dnsEnable);
      }


    return (
        <div className="column">
            <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
                <div>{t("dns-configuration")}</div>
                <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
            </div>

            {isOpen && (
                <div className="priority-section">

                    <div className='selection-input'>
                        <div className='sub-header'>
                            <div>{t("dns-enable")}</div>
                            <input className={`checkbox ${dnsEnable ? 'checked' : ''}`} type="checkbox" checked={dnsEnable} onChange={(e) => setdnsEnable(e.target.checked)} />
                            <img className='tik' src="/assets/img/tik.png" alt="" />
                        </div>
                    </div>

                    <div className='sub-header'>{t("dns")} 1</div>

                    <input name='dns1' className="input" type="text" onChange={handleDns1Change} value={DNS1} disabled={!dnsEnable}/>

                    <div className='sub-header'>{t("dns")} 1</div>

                    <input name='dns2' className="input" type="text" onChange={handleDns2Change} value={DNS2} disabled={!dnsEnable}/>

                    <div className='save-button'>{t("save")}</div>
                </div>
            )}
        </div>
    );
};

export default DNSConfiguration;


