import { useTranslation } from "react-i18next";
import React, { useState, useEffect } from 'react';
import { toast } from "react-toastify";

const OcppSettings: React.FC = () => {
  const { t, i18n } = useTranslation();
  const [isOpen, setIsOpen] = useState(true);
  const [domainName, setDomainName] = useState("");
  const [port, setPort] = useState("");
  const [sslEnable, setSslEnable] = useState("");
  const [path, setPath] = useState("");
  const [chargePointId, setChargePointId] = useState("");
  const [authorizationKey, setAuthorizationKey] = useState("");
  const [certFileError, setCertFileError] = useState<string | null>(null);
  const [domainError, setDomainError] = useState<string | null>(null);
  const [portError, setPortError] = useState<string | null>(null);
  const [file, setFile] = useState<File | null>(null);

  const [fileName, setFileName] = useState('');





  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  function isValidDomainOrIp(value: string): boolean {
    const domainPattern = /^(?!-)[A-Za-z0-9-]+(\.[A-Za-z0-9]+)*(\.[A-Za-z]{2,})$/;
    const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
    return domainPattern.test(value) || ipPattern.test(value);
  }

  function isValidPort(port: string): boolean {
    const portNumber = parseInt(port, 10);
    return portNumber >= 0 && portNumber <= 65535;
  }

  function convertFileToBase64(file: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        const result = reader.result as string;
        resolve(result.split(',')[1] || '');
      };
      reader.onerror = error => reject(error);
    });
  }

  function handleDomainChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setDomainName(value);
    if (isValidDomainOrIp(value)) {
      setDomainError(null);
    } else {
      setDomainError(t("Invalid domain name or IP address"));
    }
  }

  function handlePortChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setPort(value);
    if (isValidPort(value)) {
      setPortError(null);
    } else {
      setPortError(t("Port number must be between 0 and 65535"));
    }
  }


  function handleFileChange(e: React.ChangeEvent<HTMLInputElement>) {
    if (e.target.files && e.target.files.length > 0) {
      setFileName(e.target.files[0].name);
    }

    const selectedFile = e.target.files ? e.target.files[0] : null;
    if (selectedFile) {
      const allowedExtensions = ['pem', 'cert', 'crt', 'cer', 'der', 'pfx', 'p12', 'key', 'p7b', 'p7c', 'jks', 'keystore', 'ssp', 'sst'];
      const fileExtension = selectedFile.name.split('.').pop()?.toLowerCase();

      if (fileExtension && allowedExtensions.includes(fileExtension)) {
        setFile(selectedFile);
        setCertFileError(null);
      } else {
        setCertFileError(t("certificate-invalid"));
        toast.error(t("certificate-invalid"));
        setFile(null);
      }
    } else {
      setCertFileError(t("certifcate-not-selected-will-delete"));
    }
  }

  async function SaveOcppSettings() {

  }

  function handleDeleteCertificate() {
    setFileName("");
    setFile(null);
    SaveOcppSettings();
  }

  return (
    <div className="column">
      <div className="header" onClick={toggleDropdown} style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}>
        <div>{t("ocpp-settings")}</div>
        <div className={`arrow ${isOpen ? "up" : "down"}`} style={{ marginLeft: '10px' }}></div>
      </div>

      {isOpen && (
        <div className="priority-section">

          <div className='sub-header'>{t("ip-or-domain-name")}</div>

          <input name='domainName' className={`input ${domainError ? 'is-invalid' : ''}`} type="text" onChange={handleDomainChange} value={domainName} />

          <div className='sub-header'>{t("Port")}</div>

          <input name='port' className={`input ${portError ? 'is-invalid' : ''}`} type="text" onChange={handlePortChange} value={port} />

          <div className='sub-header'>{t("ssl-enable")}</div>

          <div className='selection-input'>
            <img className='ok-down' src="/assets/img/ok-down.png" alt="" />
            <select className='custom-select' onChange={(e) => setSslEnable(e.target.value)} value={sslEnable}>
              <option value="Enable">{t("enable")}</option>
              <option value="Disable">{t("disable")}</option>
            </select>
          </div>

          <div className='sub-header'>{t("path")}</div>

          <input name='path' className="input" type="text" onChange={(e) => setPath(e.target.value)} value={path} />

          <div className='sub-header'>{t("charge-point-id")}</div>

          <input name='chargePointId' className="input" type="text" onChange={(e) => setChargePointId(e.target.value)} value={chargePointId} />

          <div className='sub-header'>{t("authorization-key")}</div>

          <input name='authorizationKey' className="input" type="text" onChange={(e) => setAuthorizationKey(e.target.value)} value={authorizationKey} />

          <div className='sub-header'>{t("certificate-import")}</div>

          <div className="file-upload">
            <input name="certfile" className={`input-certiface ${fileName ? 'is-valid' : 'is-invalid'}`} type="file" onChange={handleFileChange} accept=".pem,.cert,.crt,.cer,.der,.pfx,.p12,.key,.p7b,.p7c,.jks,.keystore,.ssp,.sst"/>
            <label htmlFor="certfile" className="custom-file-upload">{t("select-file")}</label>
            <span className="file-name">{fileName}</span>
            <img className="delete" src="/assets/img/delete.png" alt="" onClick={handleDeleteCertificate}/>
          </div>

          <div className='save-button'>{t("save")}</div>
        </div>
      )}
    </div>
  );
};

export default OcppSettings;


