import React, { useState } from 'react';
import { useTranslation } from "react-i18next";
import { useNavigate } from 'react-router-dom';

type DashboardSectionProps = {
    active: string;
};

const DashboardSection: React.FC<DashboardSectionProps> = ({ active }) => {
    const [menuCollapsed, setMenuCollapsed] = useState(false);
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();

    const toggleMenu = () => {
        setMenuCollapsed(!menuCollapsed);
    };

    function openCharge() {
        navigate("/charge")
    }

    function openHardware() {
        navigate("/hardware")
    }

    function openSoftware() {
        navigate("/software")
    }

    function openDevice() {
        navigate("/device")
    }

    function openProfile() {
        navigate("/profile")
    }

    function openDashboard() {
        navigate("/dashboard")
    }


    return (
        <div className={`dashboard-container ${menuCollapsed ? 'collapsed' : ''}`}>
            <div className={`hera-logo-dashboard ${menuCollapsed ? 'collapsed' : ''}`}>
                {!menuCollapsed && <img className="img-hera-logo" src="/assets/img/logo.png" alt="Logo" />}
                <img className={`img-menu ${menuCollapsed ? 'collapsed' : ''}`} src="/assets/img/menu.png" alt="Menu" onClick={toggleMenu} />
            </div>

            <div className={`dashboard-menu ${menuCollapsed ? 'collapsed' : ''}`}>
                <div className="section" onClick={openDashboard}>
                    <img className="dashboard-img" src={active === "dashboard" ? "/assets/img/dashboard_active.png" : "/assets/img/dashboard_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "dashboard" ? "active" : ""}`}>{t("dashboard")}</span>}
                </div>

                <div className="section" onClick={openCharge}>
                    <img className="dashboard-img" src={active === "charge" ? "/assets/img/charge_active.png" : "/assets/img/charge_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "charge" ? "active" : ""}`}>{t("charge-details")}</span>}
                </div>

                <div className="section" onClick={openHardware}>
                    <img className="dashboard-img" src={active === "hardware" ? "/assets/img/hardware_active.png" : "/assets/img/hardware_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "hardware" ? "active" : ""}`}>{t("hardware-settings")}</span>}
                </div>

                <div className="section" onClick={openSoftware}>
                    <img className="dashboard-img" src={active === "software" ? "/assets/img/software_active.png" : "/assets/img/software_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "software" ? "active" : ""}`}>{t("software-settings")}</span>}
                </div>

                <div className="section" onClick={openDevice}>
                    <img className="dashboard-img" src={active === "device" ? "/assets/img/device_active.png" : "/assets/img/device_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "device" ? "active" : ""}`}>{t("device-status")}</span>}
                </div>

                <div className="section" onClick={openProfile}>
                    <img className="dashboard-img" src={active === "profile" ? "/assets/img/profile_active.png" : "/assets/img/profile_disable.png"} alt="" />
                    {!menuCollapsed && <span className={`dashboard-text ${active === "profile" ? "active" : ""}`}>{t("profile")}</span>}
                </div>
            </div>
        </div>
    );
};

export default DashboardSection;
